@import '../../style/button.scss';

* {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
  margin-top: 50px;
  position: relative;
}

ul,
li,
dl {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.app {
  width: 1100px;
  margin: 0 auto;
  padding: 50px 0;
  position: relative;

  .char__content {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 650px 425px;
    gap: 25px;
    //align-items: start;
  }
}

.bg-decoration {
  position: absolute;
  right: -174px;
  bottom: -70px;
  z-index: -1;
}



